input,
textarea {
  background: #fff;
  border: 1px solid #d1d1d1;
  color: #686868;
  padding: 22px 16px;
  font-size: 14px;
  box-sizing: border-box;
}

::-webkit-input-placeholder {
  /* Edge */
  color: $text;
}

:-ms-input-placeholder {
  /* Internet Explorer 10-11 */
  color: $text;
}
::placeholder {
  color: $text;
  font-size: 16px;
  font-family: "Montserrat";
  font-weight: 600;
}
.contactAndInfos {
  width: 100%;
  padding: 0px;
  display: flex;
  flex-direction: column;
}
.itemInput {
  display: flex;
  flex-direction: column;
  margin-bottom: 16px;
  input,
  textarea {
    width: initial;
    border: none;
    padding-left: 38px;
  }
  span {
    position: absolute;
    margin-top: 18px;
    margin-left: 7px;
  }
}
.formContato {
  background-color: #f2f2f2;
  height: auto;
  padding: 30px;
  max-width: 70%;
  margin: 0 auto;
  display: flex;
  flex-direction: column;

  @include for-desktop-up {
    margin: 0;
    width: 100%;
    height: 60vh;
  }

  button {
    @include buttonDefault($primary, #fff);
    width: 100%;
  }

  textarea {
    height: 364px;
  }

  p {
    font-size: 12px;
    color: $text;
    font-weight: 600;
    margin-top: 30px;
    display: block;
    width: 100%;
    line-height: 18px;
  }
}
.two_coluns {
  display: flex;
  flex-direction: column;
  justify-content: space-between;

  div {
    width: 100%;
    @include for-desktop-up {
      width: 95%;
    }
  }

  @include for-desktop-up {
    flex-direction: row;
  }
}
