.map {
  padding: 16px;
  iframe {
    max-width: 100%;
    width: 100% !important;
    height: 400px;
    margin: 0 auto;
  }
  @include for-desktop-up {
    width: 100%;
    padding: 16px 0 0 0;
  }
}

.titleContact {
  margin: 80px 0px;
  width: auto;

  h4 {
    text-transform: uppercase;
    font-weight: bold;
    text-align: center;
    padding: 0 10px;
    font-size: 24px;

    @include for-desktop-up {
      padding: 0 230px;
    }
  }
}

.lineForm {
  display: flex;
  flex-direction: column;

  @include for-desktop-up {
    display: flex;
    flex-direction: row;
  }
}
.infos {
  width: 85%;
  padding: 12px;
  margin-top: 60px;

  h4 {
    text-transform: uppercase;
    font-weight: bold;
    text-align: left;
    padding: 0 10px;
    font-size: 24px;
    margin-bottom: 36px;
  }

  .infosData {
    margin-bottom: 36px;
    ul {
      padding: 0;
      list-style: none;

      li {
        display: flex;
        align-items: center;
        margin-bottom: 16px;
        color: $text;
        font-size: 14px;
      }
    }
    span {
      color: $secondary;
      margin-right: 10px;
    }

    border-bottom: 1px solid #ccc;
  }
  @include for-desktop-up {
    max-width: 300px;
    margin-left: 30px;
    padding: 0;
    margin-top: 0px;
  }
}
